.stars {
  display: flex;
  justify-content: space-between;

  width: 96px;

  &__star {
    background: url(../images/star.svg) center no-repeat;

    width: 16px;
    height: 16px;
  }

  @for $i from 1 through 5 {
    &--#{$i} :nth-child(-n + #{$i}) {
      background-image: url(../images/star-active.svg);
    }
  }
}
